#sidebar {
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background-color: #f4f4f4;
    padding: 20px;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
}

.toc h2 {
    font-size: 18px;
    margin-bottom: 15px;
    color: #333;
}

.toc ul {
    list-style-type: none;
    padding-left: 0;
}

.toc li {
    margin-bottom: 10px;
}

.toc a {
    text-decoration: none;
    color: #007bff;
    font-size: 14px;
}

.toc a:hover {
    text-decoration: underline;
}
